"use client"

import { exerciciosPorTreino } from "./components/ExercicioList"
import { useState } from "react";
import exerciciosPorTreino from "./exercicios";

export default function TreinoMenu() {
  const [treinoSelecionado, setTreinoSelecionado] = useState<string | null>(null);

  return (
    <div className="flex h-screen bg-gray-100">
      <aside className="w-64 bg-white shadow-md p-4">
        <h2 className="text-xl font-semibold mb-4">Treinos</h2>
        <ul>
          {Object.keys(exerciciosPorTreino).map((treino) => (
            <li key={treino}>
              <button
                className={`w-full text-left p-2 rounded-md transition-colors ${
                  treinoSelecionado === treino ? "bg-blue-500 text-white" : "bg-gray-200"
                }`}
                onClick={() => setTreinoSelecionado(treino)}
              >
                {treino.replace(/_/g, " ")}
              </button>
            </li>
          ))}
        </ul>
      </aside>
      <main className="flex-1 p-4">
        {treinoSelecionado ? (
          <div>
            <h2 className="text-2xl font-bold mb-4">{treinoSelecionado.replace(/_/g, " ")}</h2>
            <ul>
              {exerciciosPorTreino[treinoSelecionado].map((exercicio) => (
                <li key={exercicio.nome} className="mb-4">
                  <h3 className="text-lg font-semibold">{exercicio.nome}</h3>
                  <video controls className="w-full max-w-md">
                    <source src={exercicio.videoUrl} type="video/mp4" />
                    Seu navegador não suporta o vídeo.
                  </video>
                </li>
              ))}
            </ul>
          </div>
        ) : (
          <p>Selecione um treino para ver os exercícios.</p>
        )}
      </main>
    </div>
  );
}
